import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import DataTable from 'react-data-table-component';
import { Link } from 'react-router-dom';

const ProductListing = () => {
    const [loading, setLoading] = useState(true)
    const [normalData, setNormalData] = useState([]);
    const [foodItems, setFoodItems] = useState([]);
    async function getMydat(params) {
        let response = await fetch("https://techtastebackend.onrender.com/foodData", {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
            },
        });
        response = await response.json();

        setFoodItems(response?.reverse());
        setNormalData(response)
        setLoading(false)
    }
    const handleDelete = async (id) => {
        let response = await fetch(`https://techtastebackend.onrender.com/delete-product-by-id/${id}`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
        })

        await getMydat()
        /////change logic here

    }
    useEffect(() => {

        getMydat();
    }, [window.location]);


    const columns = [
        {
            name: "",
            selector: row => row.number,
            grow: 1
        },
        {
            name: 'Image',
            cell: (row) => {
                return <>
                    <img style={{
                        height: "40px", width: "40px", borderRadius: "50%"
                    }} src={row.img} />
                </>
            },
            grow: 1
        },
        {
            name: 'Name',
            selector: row => row.name,
            grow: 3
        },
        {
            name: 'Price options',
            cell: row => {
                let x = ""
                row.options.map((el, index) => {
                    if (index === 0 || index === row.options.length) {
                        x += `${el}`
                    } else {
                        x += `,${el}`
                    }

                })
                return <>
                    {x}
                </>
            },
            grow: 2
        },
        {
            name: 'Actions',
            cell: (row) => {
                return <>
                    <button className='btn btn-sm btn-danger' onClick={() => {
                        handleDelete(row._id)
                        setTimeout(async () => {
                            await getMydat()
                        }, 2000)
                    }}>Delete</button>
                </>
            }
        }
    ];


    return (
        <div>
            <div>
                <Navbar />
            </div>
            <div className='card mx-3 mt-2'>
                <div className='d-flex justify-content-between mx-3 my-2 justify-content-between'>
                    {/* <h2 className='text-center ' style={{ visibility: "hidden" }}>Products</h2> */}
                    <h2 className='text-center '>Products</h2>
                    <h2 className='text-center pr-2'>
                        <Link to="/addfooddata">
                            <button className='btn btn-sm btn-success '>Add New Product </button>
                        </Link>
                    </h2>

                </div>
                <hr />
                <div >
                    <DataTable
                        columns={columns}
                        noHeader
                        data={foodItems?.map((item, index) => ({ ...item, number: index + 1 }))}
                        pagination
                        paginationComponentOptions={
                            {

                                rowsPerPageText: "Products per page"
                            }
                        }
                        highlightOnHover
                    />
                </div>
            </div>
        </div>
    )
}

export default ProductListing