import React from "react";
import Delete from "@material-ui/icons/Delete";
import { useCart, useDispatchCart } from "../components/ContextReducer";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import useRazorpay from "react-razorpay";

export default function Cart() {
  const [Razorpay] = useRazorpay();
  const navigate = useNavigate();
  let data = useCart();
  let dispatch = useDispatchCart();
  if (data.length === 0) {
    return (
      <div>
        <div className="m-5 w-100 text-center text-light fs-3">
          The Cart is Empty!
        </div>
      </div>
    );
  }
  function inrToPaise(amountInRupees) {
    // Ensure the input is a valid number
    if (isNaN(amountInRupees)) {
      return "Please enter a valid number for INR";
    }

    // Convert INR to paise (1 INR = 100 paise)
    const amountInPaise = amountInRupees * 100;

    return amountInPaise;
  }
  const handleCheckOut = async () => {
    let userEmail = localStorage.getItem("userEmail");

    let response = await fetch("https://techtastebackend.onrender.com/api/auth/orderData", {

      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        order_data: data,
        email: userEmail,
        order_date: new Date().toDateString(),
      }),
    });
    console.log("JSON RESPONSE:::::", response.status);
    if (response.status === 200) {
      dispatch({ type: "DROP" });
      toast.success("Order Successfull!");
      navigate("/myorder  ");

      // window.location.reload();
    }
  };
  const myFun = (orderId) => {
    var options = {
      "key": "rzp_test_N7iKIbYFI3runu", // Enter the Key ID generated from the Dashboard
      "amount": "1", // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
      "currency": "INR",
      "name": "Tech taste", //your business name
      "description": "Test Transaction",
      "image": "https://i.ibb.co/qYFbR0M/Screenshot-2024-03-06-001657.png",
      "order_id": orderId, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
      "handler": function (response) {
        // alert(response.razorpay_payment_id);
        // alert(response.razorpay_order_id);
        // alert(response.razorpay_signature)
        if (response.razorpay_order_id) {
          handleCheckOut()
        } else {
          alert("something went wrong")
        }
      },
      "prefill": { //We recommend using the prefill parameter to auto-fill customer's contact information, especially their phone number
        "name": "Meenakshi Sharma", //your customer's name
        "email": "exampleemail.com",
        "contact": "8320506797"  //Provide the customer's phone number for better conversion rates 
      },
      "method": "googlepay",
      "notes": {
        "address": "Razorpay Corporate Office"
      },
      "theme": {
        "color": "#3399cc"
      }
    };
    var rzp1 = new Razorpay(options);
    rzp1.open();

  }
  const PaymentStartFunc = async (amount) => {
    try {
      await axios.post("https://techtastebackend.onrender.com/api/auth/razorpay-order-create", { amount: inrToPaise(amount) }).then((res) => {
        const orderData = res.data.order

        if (orderData) {
          console.log(orderData.id);
          myFun(orderData.id)
        }
      }).catch((err) => {
        console.log(err);
      })
    } catch (error) {
      console.log(error);
    }
  }
  let totalPrice = data.reduce((total, food) => total + food.price, 0);
  return (
    <div>

      <div className="container m-auto mt-5 table-responsive  table-responsive-sm table-responsive-md">
        <table className="table table-hover ">
          <thead className=" text-success fs-4">
            <tr>
              <th scope="col">#</th>
              <th scope="col">Name</th>
              <th scope="col">Quantity</th>
              <th scope="col">Option</th>
              <th scope="col">Amount</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {data.map((food, index) => (
              <tr>
                <th scope="row" className="text-light">
                  {index + 1}
                </th>
                <td className="text-light">{food.name}</td>
                <td className="text-light">{food.qty}</td>
                <td className="text-light">{food.size}</td>
                <td className="text-light">{food.price}</td>
                <td className="text-light">
                  <button type="button" className="btn p-0 text-light">
                    <Delete
                      onClick={() => {
                        dispatch({ type: "REMOVE", index: index });
                        toast.success("removed from the cart");
                      }}
                    />
                  </button>{" "}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div>
          <h1 className="fs-2 text-success">Total Price: {totalPrice}/-</h1>
        </div>
        <div>
          <button className="btn bg-success mt-5 " onClick={() => PaymentStartFunc(totalPrice)}>
            {" "}
            Order Now{" "}
          </button>
        </div>
      </div>
    </div>
  );
}
